body{
  background-color:rgb(156, 79, 228);
  align-items: center;
  justify-content: center;
  font-family: sans-serif;
  padding: 15%;
  display: flex;
}
.inputs{
  background-color: aliceblue;
  border-radius: 10px;
  width: 300px;
  padding: 20px;
  display: flex;
  flex-direction: row;
  justify-content: space-around;
}
input{
  width: 200px;
  height: 30px;
  border: 1px solid rgb(164, 161, 161);
  border-radius: 5px;
}
input:hover{
  border: 1px solid rgb(104, 101, 101);
}
.buttonAdd{
  width: 60px;
  height: 30px;
  background-color: rgb(156, 79, 228);
  color: aliceblue;
  border: none;
  border-radius: 5px;
}
.Lists{
  background-color: aliceblue;
  border-radius: 10px;
  margin-top: 30px;
  width: 300px;
  padding: 20px;
  }
  ul li{
    display:flex;
    flex-direction: row;
    justify-content:space-between;
    border-bottom: 1px solid rgb(164, 161, 161);
    align-items: center;
    margin-left: -25px;
    margin-bottom: 10px;
  }
  .text{
    overflow:auto;
  }
.buttonDelete{
  width: 40px;
  height: 40px;
  background-color: rgb(156, 79, 228);
  color: aliceblue;
  border: none;
  border-radius: 5px;
}